.contacts-position {
  position: absolute;
  top: -353px;

  @media screen and (min-width: 768px) {
    position: static;
  }
}

.contakts-name-hidden {
  position: absolute;
  top: 0;
  left: 0;
}

.contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #84a178;
  position: relative;
  padding-top: 75px;
  margin-bottom: -1px;
  @media screen and (min-width: 768px) {
    padding-top: 238px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 414px;
  }

  &-list {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (min-width: 768px) {
      position: absolute;
      top: -278px;
      left: 32px;
      right: 32px;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      flex-direction: row;
    }

    @media screen and (min-width: 1200px) {
      top: -105px;
      left: 16px;
      right: 16px;
    }
  }

  &-list_items {
    width: 280px;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    background-color: #fcfcfc;
    display: flex;
    flex-direction: column;
    padding: 40px 32px 24px 32px;
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%), 0px 4px 4px rgb(0 0 0 / 6%), 1px 4px 6px rgb(0 0 0 / 16%);

    @media screen and (min-width: 768px) {
      width: 224px;
    }

    @media screen and (min-width: 1200px) {
      width: 368px;
    }
  }

  &-list__name {
    background: #f8d24e;
    border-radius: 6px;
    padding: 4px 22px;
    font-weight: 500;
    font-size: 12px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.04em;
    color: #131313;
    margin-bottom: 8px;
    width: fit-content;

    @media screen and (min-width: 768px) {
      font-size: 16px;
      padding: 4px 14px;
    }
  }

  &_name {
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    letter-spacing: 0.04em;
    color: #131313;
    margin-bottom: 12px;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
      font-size: 16px;
    }
  }

  &_address {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 183%;
    letter-spacing: 0.04em;
    color: #d6936d;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
      font-size: 16px;
    }
  }


  &-work_list {
    margin-bottom: 8px;
  }


 &-work_list {
      margin-bottom: 8px;
    }

    &-adres_tel {
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 150%;
      color: #131313;
      margin-bottom: 6px;
      text-decoration: none;

      @media screen and (min-width: 768px) {
        margin-bottom: 4px;
        font-size: 14px;
      }

      @media screen and (min-width: 1200px) {
        font-size: 16px;
      }
    }

    &-adres_mail {
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 150%;
      color: #131313;
      margin-right: 5px;
      text-decoration: none;

      @media screen and (min-width: 768px) {
        font-size: 14px;
      }

      @media screen and (min-width: 1200px) {
        font-size: 16px;
      }
    }

    &-soc {
      @media screen and (min-width: 1200px) {
        flex-direction: column;
        display: flex;
      }
    }
  }


.contacts-list_items:nth-child(3) {
  margin-top: 16px;

  @media screen and (min-width: 768px) {
    margin-top: 0;
  }
}

.contacts_address:hover,
.contacts_address:focus {
  text-decoration: underline;
  cursor: pointer;
  transform: scale(1.05);
}

.contacts-list_items:not(:last-child) {
  @media screen and (min-width: 768px) {
    margin-right: 16px;
  }

  @media screen and (min-width: 1200px) {
    margin-right: 32px;
  }
}

.contacts_address-dop {
  position: relative;
  margin-top: 24px;
  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    margin-top: 32px;
    margin-bottom: 32px;
  }
}

.contacts_address-dop::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 0;
  border: 1px solid #e1e1e1;
  left: 0;
  bottom: 0;
}

.contacts-work_items {
  font-weight: 500;
  font-size: 12px;
  line-height: 186%;
  color: #131313;

  @media screen and (min-width: 768px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }

  &-time {
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: #907e82;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
      font-size: 16px;
    }
  }
}

.contacts-button {
  margin-top: 20px;
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  @media screen and (min-width: 1200px) {
    margin-top: 60px;
    margin-bottom: 120px;
  }

  &_local {
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 196px;
    height: 42px;
    margin-left: 62px;
    margin-right: 62px;
    background: #f8d24e;
    border-radius: 10px;
    font-weight: 600;
    font-size: 14px;
    line-height: 129%;
    border: none;

    align-items: center;
    text-align: center;
    color: #000000;
    transition: color 250ms cubic-bezier(0.29, 0.87, 0.73, 0.12),
      border 250ms cubic-bezier(0.29, 0.87, 0.73, 0.12),
      background-color 250ms cubic-bezier(0.29, 0.87, 0.73, 0.12);

    @media screen and (min-width: 768px) {
      margin-bottom: 0;

      margin-right: 16px;
    }
  }

  &_franc {
    align-items: center;
    justify-content: center;
    width: 196px;
    height: 42px;
    margin-left: 62px;
    margin-right: 62px;
    border: 2px solid #f8d24e;
    border-radius: 10px;
    background-color: transparent;
    font-weight: 600;
    font-size: 14px;
    line-height: 129%;
    display: flex;
    align-items: center;
    text-align: center;
    color: #000000;
    transition: color 250ms cubic-bezier(0.29, 0.87, 0.73, 0.12),
      border 250ms cubic-bezier(0.29, 0.87, 0.73, 0.12);

    @media screen and (min-width: 768px) {
      margin-left: 0;
    }
  }
}

.contacts-button_franc:hover {
  border: 2px solid #0a8817;
  border-radius: 10px;
  transform: scale(1.1);
}

.contacts-button_local:hover {
  background: #0a8817;
  border-radius: 10px;
  transform: scale(1.1);
}

.contacts-button_franc:focus {
  border: 2px solid #0a8817;
  border-radius: 10px;
  color: #ffffff;
  transform: scale(1.1);
}

.contacts-button_local:focus {
  background: #0a8817;
  border-radius: 10px;
  color: #ffffff;
  transform: scale(1.1);
}

.contacts-button_franc span {
  margin-right: 7px;
}

.contacts-button_local span {
  margin-right: 7px;
}

.contacts_icon {
  fill: currentColor;
}

.contacts-adres_icon-mail,
.contacts-adres_icon-tel {
  opacity: 0;
  fill: currentColor;
}

.contacts-adres_tel:hover .contacts-adres_icon-tel,
.contacts-adres_tel:focus .contacts-adres_icon-tel {
  opacity: 1;
}
.contacts-adres_mail:hover .contacts-adres_icon-mail,
.contacts-adres_mail:focus .contacts-adres_icon-mail {
  opacity: 1;
}

.contacts-adres_mail:hover,
.contacts-adres_tel:focus,
.contacts-adres_mail:focus,
.contacts-adres_tel:hover {
  color: blueviolet;
}

.contacts_address span {
  @media screen and (min-width:1200px) {
    border-radius: 5px 5px 5px 5px;
      visibility: hidden;
      position: absolute;
      top: -100px;
      left: 125px;
      background: #fff;
      box-shadow: -2px 2px 10px -1px #333;
      border-radius: 5px;
      // width: 20%;
      z-index: 1;
  }
  
}

.contacts_address:hover span {
  visibility: visible;
}