*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary_text-color: #fcfcfc;
  --secondary-black-color: #131313;
  --primari-bg-color: #84a278;
  --primari-light-bg-color: #84a178;
  --secondary-bg-color: #fcfcfc;
  --yellow-bg-color: #efd478;
  --dark-yellow-bg-color: #f8d24e;
  --dark-green-bg-color: #0a8817;
  --gray-bg-color: #ababab;
  --pistachio-bg-color: #d6936d;
  --dark-pistachio-bg-color: #d5753f;
  --main-radio-btn-color: #d9d9d9;
  --main-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --size-mobile: 480px;
  --size-tablets: 768px;
  --size-pc: 1200px;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 480px) {
  .container {
    width: var(--size-mobile);
  }
}

@media (min-width: 768px) {
  .container {
    width: var(--size-tablets);
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: var(--size-pc);
    padding-left: 16px;
    padding-right: 16px;
  }
}

.backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 1;
  background-color: rgba(80, 77, 77, .5);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal-button {
  color: var(--secondary-black-color);
  background-color: var(--dark-yellow-bg-color);
  cursor: pointer;
  text-align: center;
  letter-spacing: .02em;
  border: none;
  border-radius: 10px;
  align-items: center;
  margin: 0 auto;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21429;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.modal-button:hover {
  background-color: var(--dark-green-bg-color);
}

.modal-button:focus {
  color: var(--primary_text-color);
  background-color: var(--dark-green-bg-color);
  fill: var(--primary_text-color);
  outline: none;
}

.button-close {
  width: 30px;
  height: 30px;
  color: var(--primary-text-tittle-color);
  background-color: var(--background-maintittle-color);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

.button-close__icon {
  width: 100%;
  height: 100%;
  fill: currentColor;
  justify-content: center;
  display: flex;
}

.button-close:hover .button-close__icon, .button-close:focus .button-close__icon {
  fill: var(--dark-green-bg-color);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  display: block;
}

body {
  font-family: var(--main-font);
}

textarea {
  resize: none;
}

html {
  scroll-behavior: smooth;
}

